<template>
    <div class="re-screenshot-container">
        <slot v-if="hasSlots" name="custom"></slot>
        <button v-else class="re-screenshot" @click="saveAsImage">{{msg}}</button>
    </div>
</template>

<script>
import html2canvas from '../../lib/html2canvas.main'

export default {
  name: 're-screenshot',
  props: {
    msg: { default: '', type: String },
    fileName: { default: '', type: String },
    scaleBy: { default: 5, type: Number },
    targetId: { required: true, type: String }
  },
  data () {
    return {
      options: {
        allowTaint: true,//
        tainttest: true, //
        logging: false, //false
        scale: '', // scale 
        canvas: '', // canvas
        width: '', //dom 
        height: '' //dom 
      }
    }
  },
  computed: {
    hasSlots () {
      let isExist = false
      for (let key in this.$slots) {
        if (key === 'custom') {
          isExist = true
        }
      }
      return isExist
    }
  },
  methods: {
    saveAsImage () {
      let imageUrl = ''
      let dom = document.getElementById(this.targetId) //
      let canvas = document.createElement('canvas');  //canvas 
      let width = dom.offsetWidth;  // (dom 
      let height = dom.offsetHeight; // (dom 
      let offsetTop = dom.offsetTop;  //
      let offsetLeft = dom.offsetLeft;  //
      let scale = this.scaleBy;
      let context = canvas.getContext('2d');
      canvas.width = (width + offsetLeft) * scale;   //  
      canvas.height = (height + offsetTop) * scale;  // 
      context.scale(scale, scale);
      let opts = Object.assign(this.options, { scale, width, height })
      html2canvas(dom, opts).then(canvas => {
        imageUrl = canvas.toDataURL('image/png');
        this.downLoad(imageUrl)
        this.$emit('onImageChange', imageUrl)
      });
    },
    downLoad (imageUrl) {
      if (!imageUrl) {
        this.$alert(this.$t('screenshots.alert'), 'error')
      } else {
        let a = document.createElement('a');
        a.innerHTML = ' ';
        a.id = 'download-btn-pic';
        a.download = this.fileName; //ie
        a.href = imageUrl;
        this.clickEvent(a);
      }
    },
    clickEvent (a) {
      if (document.all) {
        a.click();
        a.remove()
      } else {
        let evt = document.createEvent("MouseEvents");
        evt.initEvent("click", true, true);
        a.dispatchEvent(evt);
        a.remove();
      }
    }
  }
}
</script>
<style lang="scss" scoped>
    @import "../../styles/index";
    @import "../../styles/coms/screenshot";
</style>



